.App {
  background-color: white;
  min-height: 80vh;
  width: 400px;
  margin: 20px auto;
  text-align: center;
}

body {
  background-color: #62c2c8;
}

.crossed {
  text-decoration: line-through 2px red;
  background-color: #f0f0f0;
}

ul,
li {
  list-style-type: none;
  padding: 10px;
  margin: 0;
  font-family: "Montserrat", sans-serif;
}

h1 {
  font-family: "Montserrat", sans-serif;
}

.container {
  display: flex;
  justify-content: center;
}

.btn-add {
  background: #65aa06;
  margin-top: 5px;
}

.btn-delete {
  background: #d30100;
  margin-bottom: 5px;
}

.btn {
  padding: 10px;
  border: none;
  width: 70%;
  color: white;
  font-size: 16px;
  font-family: "Montserrat", sans-serif;
}
input {
  flex: 1;
  padding: 8px;
  margin: 10px;
  font-family: "Montserrat", sans-serif;
  border: solid gray 1px;
}
img {
  border-radius: 50%;
}
